.footer__info {
  display: flex;
  align-items: center;
  height: 56px;
  justify-content: space-between;
}


@media screen and (max-width: 410px) {
  .footer__info {
    height: auto;
    flex-direction: column-reverse;
    padding: 30px 0 10px;
    justify-content: start;
    gap: 30px;
  }
}